@import url(https://fonts.googleapis.com/css2?family=Bungee+Shade&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: inherit;
  margin-top: -2.7%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

html {
  height: 100%;
}

#root {
  height: 100%;
}
html {
  background-image: radial-gradient(black, rgb(44, 44, 44));
  color: white;
}

header {
  text-align: center;
  display: flex;
  flex-direction: column;
  /* flex: 1; */
}

header>hr {
  width: 100%;
}

h1 {
  font-family: 'Bungee Shade', cursive;
  font-size: 3em;
}

.App {
  /* background-image: url(./background.jpg); */
  background-size: auto;
  height: inherit;
}

#home-link {
  margin: 20px;
}

a, a:visited, a:hover, a:active {
  color: white;
}
#user-prompt {
    text-align: center;
    border: 3px solid white;
    margin: 0% 25%;
    padding: 5px;
}

#user-prompt>a {
    margin-left: 10px;
    margin-right: 10px;
}
#play-stage {
    text-align: center;
}

#genres {
    display: flex;
    flex-wrap: wrap;
    font-family: 'kavoon', cursive;
    margin: 10px;
}

#input-wrap {
    display: flex;
}

.inputs {
    flex: 1 0 25%;
    margin: 5px;
    font-size: 1.3em;
}

.error {
    color: #f24537;
    font-family: 'Open Sans', 'Helvetica Neue', 'Segoe UI', 'Calibri', 'Arial', sans-serif;
}

label {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

button {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.inputs>input[type=text] {
    width: 50%;
    box-sizing: border-box;
    font-size: .8em;
}

select {
    margin: 0 auto;
    font-family: 'Open Sans', 'Helvetica Neue', 'Segoe UI', 'Calibri', 'Arial', sans-serif;
    font-size: 18px;
    color: #60666d;
}

#hour-set #min-set {
    width: 20%;
}

.genre-option {
    flex: 1 0 25%;
    margin: 8px;
    height: 75px;
    background-color: rgb(63, 139, 189);
    font-size: 1.8em;
    border-radius: 30%;
    padding-top: 3%;
    text-align: center
}

.user-inputs {
    border: 2px solid black;
}

.genre-option>input {
    opacity: 1%;
    margin: 0;
}

.gen-selected, .genre-option:focus-within {
    background-image: linear-gradient(to top right, rgb(243, 111, 255), rgb(255, 229, 111));
    color: black;
}

.genre-option:hover, .genre-option:focus-within {
    box-shadow: 5px 10px rgb(253, 72, 141);
    cursor: pointer;
}

.genre-option:focus-within, input[type="radio"]:checked+label {
    background-color: goldenrod;
}

#create {
    box-shadow: inset 0px -3px 7px 0px #29bbff;
    background: linear-gradient(to bottom, #2dabf9 5%, #0688fa 100%);
    background-color: #2dabf9;
    border-radius: 3px;
    border: 1px solid #0b0e07;
    display: inline-block;
    cursor: pointer;
    color: #ffffff;
    font-family: Arial;
    font-size: 15px;
    padding: 9px 23px;
    text-decoration: none;
    text-shadow: 0px 1px 0px #263666;
    margin-bottom: 20px;
}

#create:hover {
    background: linear-gradient(to bottom, #c62d1f 5%, #f24537 100%);
    background-color: #c62d1f;
}

#create:disabled {
    background: linear-gradient(to bottom, #60666d 5%, #60666d 100%);
    background-color: #60666d;
    box-shadow: none;
}

#create:active {
    position: relative;
    top: 1px;
}

@media only screen and (max-width: 850px) {
    .genre-option {
        flex: 1 0 33%;
    }
}

@media only screen and (max-width:600px) {
    .genre-option {
        font-size: 1.1em;
        height: 40px;
    }
}
.song-item {
    /* border: 2px solid black; */
    margin-top: 10px;
    margin-bottom: 10px;
    width: 85%;
    text-align: left;
    padding: 10px;
    /* display: flex;
    flex-wrap: wrap; */
    display: inline-block;
    position: relative;
}

a:focus>.song-item, .song-item:hover {
    background-image: linear-gradient(to top right, rgb(111, 198, 255), pink);
}

.artist {
    word-wrap: break-word;
    display: inline-block;
    max-width: 100%;
    min-height: 2em;
    word-break: break-all;
}

#time {
    /* text-align: end; */
    /* margin-left: auto; */
    float: right;
}

.song-item>a {
    color: black;
    text-decoration: none;
}

@media only screen and (max-width: 750px) {
    #time {
        display: block;
        float: none;
    }
}
.active>h2 {
    background-color: orange;
}

.navLink {
    border: 1px solid black;
    border-right: none;
    margin: 0px;
    padding: 20px 60px;
}

.addFolder {
    border: 1px solid black;
    margin: 0px;
    padding: 20px 60px;
    margin: 8px;
}

.navLink:hover, .addFolder:hover, a:focus>article {
    background-image: linear-gradient(to top right, rgb(111, 198, 255), pink);
    /* color: white; */
}

#navBar {
    height: inherit;
    border-right: 2px solid black;
}

#recent {
    font-family: 'Bungee Shade', cursive;
}

#b0715efe-ffaf-11e8-8eb2-f2801f1b9fd1 {
    margin-top: 20px;
}
#playlistContent {
    text-align: center;
    flex: 1 1;
}

#playlistDisplay {
    display: flex;
}

#playlist-title {
    font-family: 'Bungee Shade', cursive;
}
.list-item {
    border: 2px solid white;
    margin: 2%
}
